<template>
    <div>
        <div class="canvas">
            <div class="canvas--inner" :class="[tSelected, pSelected]">
                <div
                    class="row" 
                    v-for="i in 15"
                    :key="i"
                >
                    <span v-for="j in 15" :key="j"></span>
                </div>
            </div>
        </div>

        <span @keyup.space="randomPalette()"></span>

        <div class="buttons">
            <div class="buttons__group buttons__group--tools">
                <button
                    v-for="(i, tool) in tools"
                    :key="tool"
                    :class="['button button--' + tool, { active: i }]"
                    @click="changeTool(tool)"
                >
                    <span>{{ tool }}</span>
                </button>
            </div>

            <div class="buttons__group buttons__group--palettes">
                <button
                    v-for="(i, palette) in palettes"
                    :key="palette"
                    :class="['button button--' + palette, { active: i }]"
                    @click="changePalette(palette)"
                >
                    <span>{{ palette }}</span>
                </button>
            </div>

            <div class="buttons__group buttons__group--random">'
                <button
                    class="button button--random"
                    @click="randomize()"
                >
                    <span>SURPRISE ME</span>
                </button>
            </div>
        </div>
    </div>
</template>

<script>
export default {
    name: 'minecraft',

    data() {
        return {
            tSelected: 'axe',
            pSelected: 'diamond',
            // tools: ['pickaxe', 'sword', 'shovel', 'axe'],
            // palettes: ['wood', 'stone', 'iron', 'gold', 'diamond'],
            tools: {
                pickaxe: false,
                sword: false,
                shovel: false,
                axe: false
            },
            palettes: {
                wood: false,
                stone: false,
                iron: false,
                gold: false,
                diamond: false
            }
        }
    },

    created() {
        
    },

    methods: {
        setFalse(obj) {
            let arr = Object.keys(obj);
            arr.forEach(i => obj[i] = false);
        },
        changeTool(tool) {
            this.setFalse(this.tools);
            this.tSelected = tool;
            this.tools[tool] = true;
        },
        changePalette(palette) {
            this.setFalse(this.palettes);
            this.pSelected = palette;
            this.palettes[palette] = true;
        },
        randomize() {
            this.setFalse(this.tools);
            this.setFalse(this.palettes);

            let randT = rand(Object.keys(this.tools));
            let randP = rand(Object.keys(this.palettes));

            this.tools[randT] = true;
            this.palettes[randP] = true;

            this.tSelected = randT;
            this.pSelected = randP;
        },

    }
}
/*
    @param obj [OBJECT]
    @return STRING
*/
function rand(obj) {
    return obj[Math.floor(Math.random() * obj.length)];
}
</script>





